.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 3.5%;
  background-color: #ffffff;
  transition: all 0.5s linear;
}
.header.hd-activo {
  background-color: #000000;
}
.header.hd-activo .logo h1 {
  color: #ffffff;
}
.header.hd-activo .navegacion a {
  color: #ffffff;
}
.header.hd-activo #menu-btn {
  color: #ffffff;
}
.header .logo {
  display: flex;
  align-items: center;
}
.header .logo img {
  width: 6rem;
  height: 6rem;
}
.header .logo h1 {
  font-size: 2.5rem;
  color: #000000;
  font-weight: 600;
}
.header .logo h1 span {
  font-weight: 400;
}
.header .navegacion {
  display: flex;
  gap: 1.5rem;
}
.header .navegacion a {
  font-size: 2rem;
  font-weight: 500;
  color: #000000;
  transition: all 0.1s linear;
}
.header .navegacion a:hover {
  transform: scale(1.05);
}
.header #menu-btn {
  font-size: 3.5rem;
  cursor: pointer;
  display: none;
}

@media (max-width: 1200px) {
  html {
    font-size: 55%;
  }
}
@media (max-width: 991px) {
  html {
    font-size: 50%;
  }
}
@media (max-width: 768px) {
  .header #menu-btn {
    display: inline-block;
  }
  .header #menu-btn.fa-times {
    transform: rotate(180deg);
  }
  .header .navegacion {
    position: absolute;
    top: 99%;
    right: 0;
    width: 50%;
    height: 100vh;
    flex-direction: column;
    row-gap: 4.5rem;
    align-items: center;
    border-bottom: 0.5px solid #000000;
    background-color: #000000;
    clip-path: polygon(100% 0, 100% 0, 100% 100%, 100% 100%);
  }
  .header .navegacion.activo {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }
  .header .navegacion a {
    color: #ffffff;
    font-weight: 600;
    text-transform: uppercase;
  }
  .header .navegacion a:first-of-type {
    margin-top: 4.5rem;
  }
}
.hero {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  min-width: 100%;
  min-height: 100vh;
  background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url(../images/backgrounds/hero.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.hero-contenido {
  position: absolute;
  top: 54%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
.hero-contenido h2 {
  text-transform: uppercase;
  font-size: 4.5rem;
  color: #ffffff;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  line-height: 1.2;
  margin-bottom: 6rem;
}
@media (max-width: 450px) {
  .hero-contenido h2 {
    font-size: 3.5rem;
  }
}
.hero-contenido .button {
  display: inline-block;
  background-color: #000000;
  color: #ffffff;
  padding: 1rem 5rem;
  font-size: 2rem;
  border-radius: 1rem;
  position: relative;
  cursor: pointer;
}
.hero-contenido .button:hover {
  transform: scale(1.05);
}

.nosotros {
  width: 100%;
  min-height: 100vh;
  padding: 7rem 4%;
  display: flex;
  align-items: center;
}
.nosotros .grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2.5rem;
  align-items: center;
  justify-content: center;
}
.nosotros .grid .imagen img {
  width: 100%;
  vertical-align: middle;
}
.nosotros .grid .texto h3 {
  background-color: #256edc;
  display: inline-block;
  font-weight: 400;
  font-size: 2.5rem;
  padding: 1rem 1.5rem;
  color: #ffffff;
}
.nosotros .grid .texto p {
  margin: 3rem 0;
  line-height: 1.7;
  text-align: justify;
}
.nosotros .grid .texto .redes-sociales {
  display: flex;
  column-gap: 1rem;
}
.nosotros .grid .texto .redes-sociales img {
  width: 6rem;
  height: 6rem;
}
.nosotros .grid .texto .redes-sociales img:hover {
  transform: scale(1.1);
}

@media (max-width: 768px) {
  .nosotros .grid {
    grid-template-columns: 1fr;
    gap: 0;
  }
  .nosotros .grid .texto {
    text-align: center;
  }
  .nosotros .grid .texto p {
    margin: 3rem 0;
  }
  .nosotros .grid .texto .redes-sociales {
    justify-content: center;
  }
}
.catalogo {
  position: relative;
  width: 100%;
  padding-top: 5rem;
  text-align: center;
}
.catalogo h2 {
  color: #ffffff;
  display: inline-block;
  width: 100%;
  padding: 1rem 0;
  font-size: 2.5rem;
  font-weight: 200;
  background-color: #000000;
}
.catalogo .grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  justify-content: center;
}
.catalogo .grid .producto {
  position: relative;
  overflow: hidden;
  cursor: pointer;
  border-bottom: 0.1px solid #000000;
  border-right: 0.1px solid #000000;
}
.catalogo .grid .producto img {
  display: block;
  width: 100%;
  aspect-ratio: 1/1;
  object-fit: cover;
  object-position: center;
}
.catalogo .grid .producto .contenido {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.4);
}
.catalogo .grid .producto .contenido p {
  font-size: 2rem;
  padding: 1rem;
  text-transform: uppercase;
}
.catalogo .grid .producto img,
.catalogo .grid .producto .contenido {
  transition: 200ms ease-in-out;
}
.catalogo .grid .producto > .contenido {
  opacity: 0;
}
.catalogo .grid .producto:hover > .contenido {
  opacity: 1;
}
.catalogo .grid .producto:hover .filtro {
  filter: blur(5px);
}

.opiniones {
  position: relative;
  min-height: 65vh;
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 7rem 0;
}
.opiniones h2 {
  text-align: center;
  text-transform: uppercase;
  font-weight: 400;
  padding: 1rem;
  margin-bottom: 8rem;
}
.opiniones h2::after {
  content: "";
  background: #000000;
  display: block;
  height: 0.2rem;
  width: 30rem;
  margin: 1.5rem auto 0.5rem;
}
.opiniones .wrapper {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 4rem;
  row-gap: 8rem;
  flex-wrap: wrap;
}
.opiniones .wrapper .container {
  position: relative;
  width: 35rem;
  padding: 3rem 2rem;
  border-radius: 0.3rem;
  background-color: #ffffff;
  -webkit-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.5);
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.5);
}
.opiniones .wrapper .container .profile {
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  top: -20%;
  width: 100%;
  display: block;
}
.opiniones .wrapper .container .profile .imgBox {
  position: relative;
  height: 10rem;
  width: 10rem;
  margin: auto;
  border-radius: 50%;
  overflow: hidden;
}
.opiniones .wrapper .container .profile .imgBox img {
  position: absolute;
  height: 10rem;
  width: 10rem;
  top: 0;
  left: 0;
  object-fit: cover;
}
.opiniones .wrapper .container .profile h3 {
  padding: 0.5rem 0;
  margin-top: 0.5rem;
  text-transform: uppercase;
  font-size: 2.2rem;
  font-weight: 500;
  text-align: center;
}
.opiniones .wrapper .container .profile .estrellas {
  display: flex;
  justify-content: center;
  margin-top: 0.3rem;
}
.opiniones .wrapper .container .profile .estrellas img {
  height: 3.5rem;
  width: 3.5rem;
}
.opiniones .wrapper .container .opinion-texto {
  margin-top: 10.5rem;
  font-size: 1.8rem;
  text-align: center;
}
.opiniones .wrapper .container .social-media {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 0.5rem;
  margin-top: 2rem;
}
.opiniones .wrapper .container .social-media a img {
  width: 3.5rem;
  height: 3.5rem;
}
.opiniones .wrapper .container .social-media a:hover {
  transform: scale(1.05);
}

.contacto {
  position: relative;
  min-height: 100vh;
  max-width: 100%;
  padding: 5rem 2%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-size: cover;
  background-position: top;
  overflow: hidden;
  background-image: linear-gradient(-225deg, #5D9FFF 0%, #B8DCFF 48%, #6BBBFF 100%);
}
.contacto .contenido {
  text-align: center;
}
.contacto .contenido .titulo {
  font-size: 3.5rem;
  margin-bottom: 3.5rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 450px) {
  .contacto .contenido .titulo {
    margin-bottom: 1.5rem;
  }
}
.contacto .contenido .titulo img {
  width: 8rem;
  height: 8rem;
  margin-bottom: 1.5rem;
}

.contenedor {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 10rem;
}
@media (max-width: 768px) {
  .contenedor {
    flex-direction: column;
  }
}
.contenedor .contacto-informacion .box {
  position: relative;
  padding: 1.2rem 0;
  display: flex;
  align-items: center;
}
.contenedor .contacto-informacion .box .icono,
.contenedor .contacto-informacion .box .icono a {
  width: 5rem;
  height: 5rem;
  color: #000000;
  background-color: #ffffff;
  border: 0.5px solid #000000;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  font-size: 2.5rem;
}
.contenedor .contacto-informacion .box .texto {
  display: flex;
  margin-left: 2rem;
  flex-direction: column;
  text-align: left;
}
.contenedor .contacto-informacion .box .texto h3 {
  font-weight: 500;
  font-size: 2.5rem;
  margin-bottom: 0.4rem;
}
.contenedor .contacto-informacion .box .texto p {
  font-weight: 300;
}
.contenedor .contacto-informacion .box .texto a {
  color: #000000;
  font-size: 2rem;
  font-weight: 300;
}
@media (max-width: 768px) {
  .contenedor .contacto-informacion {
    margin-bottom: 5rem;
  }
}
.contenedor .formulario {
  padding: 2rem 3rem;
  background-color: #ffffff;
  color: #000000;
  border: 0.3px solid #000000;
}
.contenedor .formulario h3 {
  font-size: 2.5rem;
  font-weight: 400;
}
.contenedor .formulario .input {
  width: 100%;
  margin-top: 2rem;
}
.contenedor .formulario .input label {
  display: none;
}
.contenedor .formulario .input input,
.contenedor .formulario .input textarea {
  width: 100%;
  padding: 0.7rem 0;
  font-size: 1.6rem;
  margin: 1rem 0;
  border: none;
  font-family: "Montserrat", sans-serif;
  border-bottom: 1.5px solid #cdcdcd;
  outline: none;
  resize: none;
}
.contenedor .formulario .input .boton-contacto {
  background-color: #000000;
  text-transform: uppercase;
  font-weight: 500;
  color: #ffffff;
  border: none;
  cursor: pointer;
  padding: 1rem 0;
}
.contenedor .formulario .input .boton-contacto:hover {
  transform: scale(1.05);
}
.contenedor .formulario .input .mensaje-enviado {
  background-color: #256edc;
  text-transform: uppercase;
  font-weight: 500;
  color: #000000;
  padding: 1rem 0;
}

.btn-wsp {
  position: fixed;
  width: 5.5rem;
  height: 5.5rem;
  line-height: 5.5rem;
  bottom: 2rem;
  left: 2rem;
  background-color: #25d366;
  color: #ffffff;
  border-radius: 50%;
  text-align: center;
  font-size: 3rem;
  box-shadow: 0px 0.1rem 1rem rgba(0, 0, 0, 0.5);
}
.btn-wsp:hover {
  text-decoration: none;
  color: #25d366;
  background-color: #ffffff;
}

.footer {
  text-align: center;
  width: 100%;
}
.footer .texto {
  font-size: 1.5rem;
  padding: 0.8rem 2%;
  font-weight: 600;
  font-family: "Raleway", sans-serif;
  color: #000000;
  line-height: 1.5;
}
.footer .texto a {
  color: #000000;
}

.contenedor-loader {
  background-color: #ffffff;
  height: 100%;
  width: 100%;
  position: fixed;
  z-index: 10000;
}
.contenedor-loader .loader {
  border: 0.5rem solid #cdcdcd;
  border-top-color: #000000;
  height: 8rem;
  width: 8rem;
  border-radius: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  -webkit-animation: girar 1.5s linear infinite;
  -o-animation: girar 1.5s linear infinite;
  animation: girar 1.5s linear infinite;
}
@media (max-width: 768px) {
  .contenedor-loader .loader {
    height: 6rem;
    width: 6rem;
  }
}
@keyframes girar {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}